.burger {
	position: relative;
	cursor: pointer;
	padding-top: 2px;
	padding-bottom: 2px;
	.burger-line {
		display: block;
		width: 45px;
		height: 2px;
		background: $dark-color;
		transition: transform .3s ease-in-out;
		overflow: hidden; }
	.line-bottom {
		margin-top: 11px;
		&::after {
			content: '';
			display: block;
			width: 45px;
			height: 2px;
			transform: translateX(-100%);
			transition: transform .8s cubic-bezier(0.445, 0.05, 0.55, 0.95);
			background: $accent-color; } }
	.line-top {
		&::after {
			content: '';
			display: block;
			width: 45px;
			height: 2px;
			transform: translateX(-100%);
			transition: transform .5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
			background: $accent-color; } }

	&.clicked {
		.line-top {
			transform: rotate(-45deg) translateY(8px); }
		.line-bottom {
			transform: rotate(45deg) translateY(-10px); } }

	&:hover {
		.line-top::after, .line-bottom::after {
			transform: translateX(0px); } } }

